.contact-contain{
    width: 100%;
    background:#fff;

}
.contact-contain h2{
    color: #000;
    padding-top: 2rem
}
.contact-form{
    width: 70%;
    margin:auto;
    text-align: start;
    
}
label{
    color: #000;
    font-size:18px;
    font-weight:bold;
    margin-top: 1em;
    
}
.txt, .txtMess{
  font-family: Arial, Helvetica, sans-serif;
  width: 100%;
  font-size: 1.3rem;
  outline: none;
  background: none;
  padding: 5px;
  border: none;
  border-bottom: 3px solid #000;
  color: #10182F;
}
.txt:focus{
    border-bottom: 3px solid rgb(132, 175, 231);
}
.txtMess:focus{
    border-bottom: 3px solid rgb(132, 175, 231);
}
.btnSubmit{
    width: 20%;
    height: 36px; 
    font-size: 18px;
    font-weight: bold;
    border: none;
    background-color: #766dff;
    border-radius:10px;
    margin-top:2rem;
    color:#fff;
    margin-bottom: 2rem;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 18px 50px -10px;
    
}
.btnSubmit:hover{
    background-color: #5d52f5;
}

@media screen and (max-width: 660px){
     .btnSubmit{
        width: 50%;
        margin: auto;
        font-size: 13px;
        margin-bottom: 2rem;
     }
     .contact-form{
         width: 80%;
     }
}
@media screen and (max-width: 1024px){
    .btnSubmit{
       width: 50%;
       margin: auto;
       font-size: 14px;
       margin-bottom: 2rem;
    }
    .contact-form{
        width: 80%;
    }
}
